.aboutme {
    width: 60%;
    text-align: justify;
    color: aliceblue;
    font-family: 'Dancing Script';
    margin-left: 20%;
}

.tittleAbout {
    font-family: 'Dancing Script';
    margin-bottom: 0rem !important; 
}

h3 {
    font-size: 1.92rem;
    line-height: 110%;
    margin: 0.9466666667rem 0 1.168rem 0;
}
.contactForm {
  font-family: "Open Sans", sans-serif;
  
  min-height: 14vh;
  height: 70vh;
  border-radius: 2%;
  margin-top: 0rem;
  max-width: 50%;
}
section {
  padding: 2em 10px;
}
h1 {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 2em;
}
div.form {
  width: 100%;
  max-width: 450px;
  border: 2px solid #fff;
  margin: 0 auto;
  padding: 20px;
}
.input-field {
  position: relative;
  top: 13px;
  max-width: 80%;
  margin-left: 10%;
}
.input-field i {
  color: #fff; 
  margin: 0px 1rem;
  font-size: 25px;
  width: 25px;
  position: relative;
}
.input-field i.fa-pen {
    top: -5rem;
  position: relative;
}
.input-field:active {color: #100244;}

input, textarea {
    margin-left: 3rem;
    background-color: transparent;
    color: #fff;
    border: none;
    border-bottom: 1px solid #fff;
    height: 3rem;
    margin: 66px 0 0px 0;
    transition: all .3s;
    width: 80%;
    padding-top: 9px;
    font-size: 16px;
}
textarea {
  color: #fff;
}
label {
  color: #fff;
  position: absolute;
  top: 2.8rem;
  left: 1.3rem;
  cursor: text;
  margin-left: 3rem;
}
.btn-block {
  margin-top: 2rem;
    text-align: center;
    position: relative;

}
.btn-block .btn {
  text-decoration: none;
  color: #fff;
  background-color: #0b0350;
  transition: .2s ease-out;
  cursor: pointer;
  border: none;
  padding: 0px 23px;
  font-size: 19px;
  margin-top: 1em;
  margin-bottom: 1em;
  border-radius: 8%;
}
.btn-block .btn i {
  float: right; 
  padding-left: 10px; 
  font-size: 16px; 
  padding-top: 3px;
}
@media (max-width: 400px) {
  div.form {width: 90%; padding: 20px 10px;}
  input, textarea {width: 73%;}
  .input-field i {margin: 0px 1rem 0px 0.6rem;}
  label {left: 0.7rem;}
}

.divForm{
    margin-left: 33%;
}

.inputCpntact {
  max-width: 80% !important;
  overflow: hidden;
}

.input-field>label {
  color: #ffffff;
  position: absolute;
  top: 0px;
  left: 10px;
  font-size: 1rem;
  cursor: text;
  transition: color .2s ease-out, -webkit-transform .2s ease-out;
  transition: transform .2s ease-out, color .2s ease-out;
  transition: transform .2s ease-out, color .2s ease-out, -webkit-transform .2s ease-out;
  -webkit-transform-origin: 0% 100%;
  transform-origin: 0% 100%;
  text-align: left;
  text-align: initial;
  -webkit-transform: translateY(12px);
  transform: translateY(12px);
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
