.my-element {
    display: inline-block;
    margin: 0 0.5rem;
  
    -webkit-animation: bounce;
  
            animation: bounce; /* referring directly to the animation's @keyframe declaration */
    -webkit-animation-duration: 2s;
            animation-duration: 2s; /* don't forget to set a duration! */
  }

  nav ul a {
    transition: background-color .3s;
    font-size: 2rem;
    color: #fff;
    display: block;
    padding: 0 83px;
    cursor: pointer;
}

.sidenav {
    position: fixed;
    width: 300px;
    left: 0;
    top: 0;
    margin: 0;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    height: 100%;
    height: calc(100% + 60px);
    height: -moz-calc(100%);
    padding-bottom: 60px;
    background-color: transparent;
    z-index: 999;
    overflow-y: auto;
    will-change: transform;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transform: translateX(-105%);
    transform: translateX(-105%);
}

.sidenav li>a {
    color: rgb(245 244 244 / 87%);
    display: block;
    font-size: 18px;
    font-weight: 500;
    height: 48px;
    line-height: 48px;
    padding: 45px 32px;
    font-family: 'Dancing Script', cursive !important;
    font-size: 2rem;
    color: rgb(255, 255, 255);
}
.site-footer
{  font-size: 15px;
  line-height: 24px;
  color: #ffffff;
  background: top;
  position: fixed;
  left: 0;
  bottom: 0%;
  width: 100%;
}
.site-footer hr
{
  border-top-color:#bbb;
  opacity:0.5
}
.site-footer hr.small
{
  margin:20px 0
}
.site-footer h6
{
  color:#fff;
  font-size:16px;
  text-transform:uppercase;
  margin-top:5px;
  letter-spacing:2px
}
.site-footer a
{
  color:#737373;
}
.site-footer a:hover
{
  color:#3366cc;
  text-decoration:none;
}
.footer-links
{
  padding-left:0;
  list-style:none
}
.footer-links li
{
  display:block
}
.footer-links a
{
  color:#737373
}
.footer-links a:active,.footer-links a:focus,.footer-links a:hover
{
  color:#3366cc;
  text-decoration:none;
}
.footer-links.inline li
{
  display:inline-block
}
.site-footer .social-icons
{
  text-align:right
}
.site-footer .social-icons a
{
    width: 40px;
    height: 40px;
    line-height: 40px;
    margin-left: -5px;
    margin-right: 10px;
    border-radius: 100%;
